<template>
  <div class="root">
    <div class="home">
      <div class="title">
        <div class="top">
          <div style="display: flex;justify-content:space-between">
            <div class="row" style="font-weight: 800;font-size: 26px;height: 50px;line-height: 50px;width:100%;">
              斑斑驾道
            </div>
            <div class="row" style="height: 50px;display: inline-block;width: 200px;">
              <el-row>
                <el-col :span="4" style="line-height: 50px;">
                  <i class="el-icon-phone"></i>
                </el-col>
                <el-col :span="20">
                  <div >
                    <div>客服热线</div>
                    <div>18888888888</div>
                  </div>
                </el-col>
              </el-row>

            </div>
          </div>
        </div>
        <div class="bottom" style="height: 50px;">
          <div v-for="(item, index) in navBars" v-text="item.title" @click="goRouter(index)" :class="index === selected ? 'active' : ''">
          </div>
        </div>
      </div>
      <div class="content">

        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<style lang="less" rel="stylesheet/less" scoped>
  .root {
    height: 100%;
    margin: 0px;
    padding: 0px;
    position: relative;
    width: 100%
  }

  .home {
    height: 100%;
    position: relative;
    padding-top: 100px;
    background-color: #f3f3f3;
  }
  .content {
    background-color: #f3f3f3;
    div{
      width: 70%;
      margin: 0px auto;
    }
  }
  .title {
    position: fixed;
    z-index: 1000;
    top: 0px;
    left: 0px;
    width: 100%;
    opacity: 1;
    background-color: #f3f3f3;
    .top{
      height: 50px;
      >div{
        width: 70%;
        margin:0px auto;
      }
    }
    .bottom {
      display: flex;
      justify-content: center;
      background-color: #00CCFF;
      color: white;
      div{
        width: 10%;
        text-align: center;
        height:50px;
        line-height: 50px;
        cursor: pointer;
      }
    }
  }
  .active{
    background-color: blue;
  }
</style>
<script>
  export default {
    data () {
      return {
        selected: 0,
        keyword: '',
        navBars: [
          {title: '首页', route: '/menu/home'},
          {title: '视频学习', route: '/menu/learn'},
          {title: '驾校练习', route: '/menu/practise'},
          {title: '模拟考试', route: '/menu/mocks'},
          {title: '软件下载', route: '/menu/download'},
          {title: '驾考指南', route: '/menu/guide'},
          {title: '关于我们', route: '/menu/about'}
        ]
      }
    },
    computed: {},
    methods: {
      goRouter (index) {
        this.selected = index
        this.$router.push(this.navBars[index].route)
      }
    },
    mounted () {
    }
  }
</script>



