import Vue from 'vue'
import Router from 'vue-router'
import menu from '../components/layout/TopMenu.vue'
import error from '../components/error/index.vue'
import login from '../components/login/index.vue'

import home from '../components/home/Index.vue'
import learn from '../components/learn/Index.vue'
import practise from '../components/practise/Index.vue'
import mocks from '../components/mocks/Index.vue'
import download from '../components/download/Index.vue'
import guide from '../components/guide/Index.vue'
import about from '../components/about/Index.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/error',
      name: 'error',
      component: error
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/menu',
      component: menu,
      children: [
        {
          path: 'home',
          component: home
        },
        {
          path: 'learn',
          component: learn
        },
        {
          path: 'practise',
          component: practise
        },
        {
          path: 'mocks',
          component: mocks
        },
        {
          path: 'download',
          component: download
        },
        {
          path: 'guide',
          component: guide
        },
        {
          path: 'about',
          component: about
        }
      ]
    },
    // 错误路径重定向到首页
    {
      path: '*',
      redirect: '/login'
    }
  ]
})



