<style lang="less" rel="stylesheet/less" scoped>
  .app-page-root {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .demo-flat-button{
    background: blue;
    padding: 10px 20px;
    border-radius: 10px;
